<template>
    <BaseAccount>
        <b-form @submit="onSubmit" @reset="onReset">
            <div class="row">
                <div class="col-md-6">
                    <b-form-group label="Заголовок">
                        <b-form-input v-model="form.title" type="text" required placeholder="Введите заголовок"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Подзаголовок">
                        <b-form-input v-model="form.subtitle" type="text" required placeholder="Введите подзаголовок"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-12">
                    <b-form-group label="Введите описание">
                        <vue-ckeditor v-model="form.description" :config="config" />
                    </b-form-group>
                </div>
                <div class="col-md-12">
                    <b-form-group label="CDN фото">
                        <b-form-input v-model="form.img" type="text" required placeholder="Введите ссылку"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-12">
                    <b-form-group label="Срок окупаемости">
                        <b-form-input v-model="form.payback" type="text" required placeholder="Введите срок окупаемости"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-12">
                    <b-form-group label="Ожидаемая доходность">
                        <b-form-input v-model="form.income" type="text" required placeholder="Введите ожидаемую доходность"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Город">
                        <b-form-select v-model="form.params.city" class="mb-3">
                            <option v-for="item in getParams.city" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Стадия">
                        <b-form-select v-model="form.params.stage" class="mb-3">
                            <option v-for="item in getParams.stage" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Индустрия">
                        <b-form-select v-model="form.params.industry" class="mb-3">
                            <option v-for="item in getParams.industry" :value="item.name">{{ item.name }}</option>
                        </b-form-select>
                    </b-form-group>
                </div>
                <div class="col-md-6">
                    <b-form-group label="Инвестиции">
                        <b-form-input v-model="form.params.price" type="number" required placeholder="Необходимая сумма инвестиций"></b-form-input>
                    </b-form-group>
                </div>
                <div class="col-md-8">
                    <b-form-group label="Бизнес-план">
                        <b-form-file
                            :state="Boolean(file)"
                            placeholder="Прикрепить бизнес план"
                            drop-placeholder="Drop file here..."
                        />
                    </b-form-group>
                </div>
                <div class="col-md-4">
                    <b-form-group label="Шаблон">
                        <b-button type="submit" variant="primary" class="mr-2">Скачать шаблон</b-button>
                    </b-form-group>
                </div>
                <div class="col-md-12">
                    <b-alert :show="dismissCountDown" dismissible variant="danger" @dismissed="dismissCountDown=0" @dismiss-count-down="countDownChanged">
                        <p>{{ error }}</p>
                        <b-progress variant="danger" :max="dismissSecs" :value="dismissCountDown" height="4px"></b-progress>
                    </b-alert>
                </div>
                <div class="col-md-8 pt-3 pb-5">
                    <b-button type="submit" variant="primary" class="mr-2">Отправить</b-button>
                    <b-button type="reset" variant="danger">Сбросить</b-button>
                </div>
            </div>
        </b-form>
    </BaseAccount>
</template>

<script>
    import BaseAccount from '@/components/BaseAccount.vue';
    import { mapActions, mapState } from 'vuex';
    import VueCkeditor from 'vue-ckeditor2';

    export default {
        name: "AccountProjectsAdd",
        data() {
            return {
                dismissSecs: 5,
                dismissCountDown: 0,
                showDismissibleAlert: false,
                error: '',
                form: {
                    title: '',
                    subtitle: '',
                    description: '',
                    img: '',
                    payback: '',
                    income: '',
                    params: {
                        city: '',
                        stage: '',
                        industry: '',
                        price: ''
                    }
                },
                config: {
                    toolbar: [
                        [
                            'Undo', 'Redo',
                            '-', 'Bold', 'Italic', 'Underline',
                            '-', 'Link', 'Unlink', 'Anchor',
                            '-', 'Format',
                            '-', 'Maximize',
                            '-', 'Table',
                            '-', 'Image',
                            '-', 'Source',
                            '-', 'NumberedList', 'BulletedList'
                        ],
                        [
                            'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock',
                            '-', 'Font', 'FontSize', 'TextColor',
                            '-', 'Outdent', 'Indent',
                            '-', 'HorizontalRule',
                            '-', 'Blockquote'
                        ]
                    ],
                    height: 300,
                    toolbarCanCollapse: false,
                    forcePasteAsPlainText: true
                }
            }
        },
        components: {
            VueCkeditor,
            BaseAccount
        },
        computed: {
            ...mapState({
                'getParams': state => state.params,
                'userId': state => state.auth.userId
            })
        },
        methods: {
            ...mapActions([
                'loadParams',
                'toggleLoad',
                'addProject'
            ]),
            countDownChanged (dismissCountDown) {
                this.dismissCountDown = dismissCountDown;
            },
            showAlert(error) {
                this.error = error;
                this.dismissCountDown = this.dismissSecs;
            },
            onSubmit(e) {
                e.preventDefault();
                this.toggleLoad(true);
                this.form.userId = this.userId;
                this.addProject(this.form).finally(() => {
                    this.toggleLoad(false);
                    this.$router.push('/account/projects');
                });
            },
            onReset(e) {
                e.preventDefault();

                this.form.title = '';
                this.form.subtitle = '';
                this.form.description = '';
                this.form.img = '';
                this.form.params.city = this.getParams.city[0].name;
                this.form.params.stage = this.getParams.stage[0].name;
                this.form.params.industry = this.getParams.industry[0].name;
                this.form.params.price = '';
            }
        },
        created() {
            this.toggleLoad(true);
            this.loadParams().then(() => {
                this.form.params.city = this.getParams.city[0].name;
                this.form.params.stage = this.getParams.stage[0].name;
                this.form.params.industry = this.getParams.industry[0].name;
            }).catch(() => {}).finally(() => {
                this.toggleLoad(false);
            });
        }
    }
</script>
